import React from 'react';
import {NavLink} from 'react-router-dom';

const Navbar=()=>{
    return(
        <>
        <div className="nav_bg mb-0">
            <div className="container-fluid">
                <div className="row">

                    <div className="col-10 mx-auto">

                    
                <nav className="navbar navbar-expand-lg ">
                <div className="container-fluid">
                    <NavLink  className="navbar-brand" to="#">MyRestaurant</NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto mb-0 mb-lg-0">
                        <li className="nav-item">
                        <NavLink activeClassName="menu_active" className="nav-link active linkcls" aria-current="page" to="/">Home</NavLink>
                        </li>
                        <li className="nav-item">
                        <NavLink activeClassName="menu_active" className="nav-link active linkcls" aria-current="page" to="/service">Services</NavLink>
                        </li>
                        <li className="nav-item">
                        <NavLink activeClassName="menu_active" className="nav-link active linkcls" aria-current="page" to="/about">About</NavLink>
                        </li>
                        <li className="nav-item">
                        <NavLink activeClassName="menu_active" className="nav-link active linkcls" aria-current="page" to="/contact">Contact</NavLink>
                        </li>
                        {/* <li className="nav-item">
                        <NavLink className="nav-link" href="#">Link</a>
                        </li> */}  
                    </ul>
                    
                    </div>
                </div>
                </nav>
                </div>

                </div>
            </div>
        </div>
        </>
    );
};
export default Navbar;